.create-a-quiz {
  margin-top: 3.2rem;
  grid-column: 2;
  grid-row: 1;
}
.create-a-quiz-hyperlink:link,
.create-a-quiz-hyperlink:visited {
  font-size: 1.8rem;
  text-decoration: none;
  margin: 0 0.5rem 0.5rem 0.5rem;
}

#signed-in-as {
  grid-column: 2;
  align-self: center;
  justify-self: center;
}
#most-recent-quiz {
  position: relative;
  grid-column: 5;
  grid-row: 2;
  align-self: start;
  justify-self: center;
  bottom: 90%;
  font-size: 18px;
  font-weight: 600;
}

#recent-quiz {
  grid-column: 5;
  grid-row: 2;
  width: 100%;
  height: auto;
  overflow-x: hidden;
  overflow-y: hidden;
  align-self: start;
  justify-self: center;
  text-align: center;

  font-size: 64px;
  font-weight: 700;
}

.edit-question-container {
  border-radius: 0% !important;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: auto;
  justify-items: center;
  margin-top: 10rem;
  width: 50% !important;
}

.edit-btn-flex {
  width: 100%;
  height: auto;
  display: grid;
  justify-self: start;
  align-self: center;
  grid-row: 13;
  grid-column: 1/4;
  grid-auto-rows: auto;
  grid-auto-columns: 1fr;
}

.add-btn {
  grid-column: 2;
  background-color: #40c057;
  width: 80%;
  border: none;
  color: #fff;
  padding: 0.8rem;
  margin: 3rem 3rem 3rem 3rem;
  font-weight: 450;
  font-size: 1.8rem;
  border-radius: 10px;
  transition: all 0.2s;
  text-decoration: none;
  justify-self: center;
  align-self: center;
}

.play-now-btn {
  position: relative;
  background-color: #2e67d1;
  width: 15rem;
  border: none;
  color: #fff;
  padding: 0.8rem;
  margin: 3rem 3rem 3rem 3rem;
  font-weight: 450;
  font-size: 1.8rem;
  border-radius: 10px;
  transition: all 0.2s;
  text-decoration: none;
  top: 10%;
  align-self: end;
  justify-self: center;
  grid-row: 3;
  grid-column: 5;
}

.log-out-btn {
  background-color: #cf2020;
  width: 15rem;
  border: none;
  color: #fff;
  padding: 0.8rem;
  margin: 8rem 3rem 3rem 3rem;
  font-weight: 450;
  font-size: 1.8rem;
  border-radius: 10px;
  transition: all 0.2s;
  text-decoration: none;
  text-align: center;
  justify-self: center;
  grid-row: 8;
  grid-column: 1 / 11;
}

.remove-quiz-flex {
  display: flex;
  width: 100%;
  grid-column: 3;
  justify-self: center;
}

.remove-btn {
  background-color: #cf2020;
  width: 80%;
  border: none;
  color: #fff;
  padding: 0.8rem;
  margin: 1rem 1rem 1rem 1rem;
  font-weight: 450;
  font-size: 1.8rem;
  border-radius: 10px;
  transition: all 0.2s;
  text-decoration: none;
  align-self: center;
}

.quiz-selection-container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: auto;
  justify-items: center;
  height: auto;
  width: 70%;
  border-radius: 8%;
  text-align: center;
  justify-content: center;
  color: rgb(0, 0, 0);
  margin: 15rem 5rem 5rem 5rem;
  border: 0.3rem solid #ffb300;
  box-shadow: 0 0 0.5rem #0000003d;

  margin-top: 10rem !important;
}

#select-a-quiz-main-div {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

#no-quizzes-text {
  position: relative;
  justify-self: center;
  align-self: center;
  grid-row: 1;
  font-size: 1.8rem;
  z-index: 5;
}

.theme-title {
  grid-column: 5;
  grid-row: 5;
  justify-self: center;
  align-self: center;
}

.theme-cs-bg button {
  box-shadow: 0 0 0.4rem #0000002f;
  border-radius: 50px;
  border: none;
  width: 4rem;
  height: 4rem;
  justify-self: center;
}

.theme-cs-grid button {
  box-shadow: 0 0 0.4rem #0000002f;
  border-radius: 50px;
  border: none;
  width: 4rem;
  height: 4rem;
  justify-self: center;
}
.white-theme {
  background-color: #ffffff;
}

/* need to test blue */

.gray-theme {
  background-color: #717171;
}

.purple-theme {
  background-color: #862e9c;
}

.theme-cs-bg {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  width: 90%;
  justify-self: center;
  height: 5rem;
  grid-column: 5;
  grid-row: 6;
  border-radius: 15px;
  background-color: #868e96;
}

.theme-cs-grid {
  position: relative;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  align-self: center;
  margin-top: 2rem;
  margin-bottom: 2rem;
  width: 60%;
  height: 5rem;
  grid-column: 5;
  grid-row: 6;
  border-radius: 15px;
}

.menu-container {
  position: absolute;
  z-index: 3;
  width: 40%;
  height: 100vh;
  border: none;
  box-shadow: 0 0 0.9rem #0000002f;
  grid-template-columns: repeat(10, auto);
  grid-template-rows: repeat(10, 1fr);
  pointer-events: auto;
  justify-content: center;
  align-items: center;
  visibility: visible;
  transform: translate(0%);
  transition: all 0.3s;
}

.menu-hidden-pos {
  transform: translate(-100%);
}

#interchangeable-scroll-container {
  display: flex;
  align-self: center;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-auto-rows: 8rem;
  margin-top: 0.5rem !important;
  position: relative;
  width: 90%;
  height: 55vh;
  z-index: 0;
  pointer-events: auto;
  visibility: visible;
  transition: all 0.5s;
  overflow-x: hidden;
  align-items: center;
  justify-items: center;
}

.new-quiz-btn {
  background-color: #51cf66;

  width: 15rem;
  border: none;
  color: #fff;
  padding: 0.8rem;
  margin: 1rem 1rem 1rem 1rem;
  font-weight: 450;
  font-size: 1.8rem;
  border-radius: 10px;
  transition: all 0.2s;
  text-decoration: none;
  align-self: center;
  z-index: 0;
}

.container .submit-btn {
  width: 30%;
}

.create-a-quiz-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 40% !important;
}
.main-font-color p {
  font-size: 1.8rem;
}
.quiz-tab-flex {
  width: 80%;
  height: 10rem;
  display: flex;
  margin-top: 0.8rem;
  margin-bottom: 0.8rem;
  align-items: center;
  border: none;
  box-shadow: 0 1.2rem 0.5rem 0.1rem #0000000a;
}

.quiz-tab {
  width: 85%;
  height: 10rem;
  display: flex;
  flex-wrap: wrap;
  margin-top: 0.8rem;
  margin-bottom: 0.8rem;
  align-items: center;
  justify-content: end;
  border: none;
}

#transparent-black-screen {
  position: absolute;
  z-index: 13;

  color: black;
  background-color: rgba(0, 0, 0, 0.3);
  width: 100vw;
  height: 110vh;
  border: none;
  visibility: visible;
  transition: all 0.5s;
}

.delete-option-btn {
  background-color: #2e67d1;
  width: 40%;
  border: none;
  color: #fff;
  padding: 0.8rem;
  margin: 1rem 1rem 1rem 1rem;
  font-weight: 450;
  font-size: 1.8rem;
  border-radius: 10px;
  transition: all 0.2s;
  text-decoration: none;
}
#delete-container {
  position: absolute;
  z-index: 15;
}

.transparent-black-screen {
  position: absolute;
  top: 0%;
  left: 0%;
  color: black;
  background-color: rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 110vh;
  border: none;
  z-index: 1;
  pointer-events: auto;
  visibility: visible;
  transition: all 0.5s;
}

.quiz-tab-options-edit {
  background-color: #ffb300;
  color: #ffffff;
  width: 10%;
  height: 100%;
}

.quiz-tab-options-delete {
  background-color: #c50000;
  color: #ffffff;
  border-bottom-right-radius: 10%;
  border-top-right-radius: 10%;
  width: 5%;
  height: 100%;
  font-size: 18px !important;
  font-weight: 400 !important;
}

.quiz-tab-options-container {
  display: flex;
  font-size: 16px;
  font-weight: 600;
  font-style: inherit;
  align-items: center;
  justify-content: center;
}

.quiz-name-tag {
  display: flex;
  font-size: 18px;
  font-weight: 600;
  font-style: inherit;
  align-items: center;
  justify-content: start;
  width: 50%;
  height: 100%;
  margin-right: 2rem;
  margin-left: 2rem;
  overflow: hidden;
}

.quiz-questions-tag {
  display: flex;
  font-size: 12px;
  font-weight: 600;
  font-style: inherit;
  align-items: center;

  width: 25%;
  height: 100%;
  margin-right: 0.2rem;
  margin-left: 0.2rem;
  overflow: hidden;
}
.main-quiz-tab-color {
  background-color: #fff;
  box-shadow: 0 0rem 0.5rem 0.2rem #0000000a;
}

.gray-quiz-tab-color {
  background-color: #808080;
}

.purple-quiz-tab-color {
  background-color: #862e9c;
}

.main-quiz-tab-color:active,
.main-quiz-tab-color:hover {
  background-color: #f0f8ff;
}

.gray-quiz-tab-color:active,
.gray-quiz-tab-color:hover {
  background-color: #b1b1b1;
}

.purple-quiz-tab-color:active,
.purple-quiz-tab-color:hover {
  background-color: #cc5de8;
}
.tab-shadow:hover,
.tab-shadow:active {
  box-shadow: 0 0.8rem 1rem 0.1rem #0000001d;
  transition: all 0.2s;
}

/* scroll bar */
::-webkit-scrollbar-thumb {
  background-color: #ffb300;
  border-radius: 5rem;
}

::-webkit-scrollbar {
  width: 5px;
  margin: 4rem;
}

.scroll-box {
  height: auto;
  width: auto;
  overflow: auto;
  visibility: hidden;
  padding: 30px;
}

.scroll-container {
  display: grid;
  grid-template-columns: repeat(1, 1fr);

  margin-top: 0.5rem !important;
  position: relative;
  width: 90%;
  height: 55vh;
  z-index: 0;
  pointer-events: auto;
  visibility: visible;
  transition: all 0.5s;
  justify-self: center;
  overflow-x: hidden;
  align-items: center;
  justify-items: center;
}
